import './About.scss';
import { useStateContext } from '../../context/ContextWrap';
import { useEffect } from 'react';

function About() {

  const { about } = useStateContext();

  useEffect(() => {
    document.title = "Anthony's Portfolio - About";
  }, [])

  return (
    <div className="about">
      <h1 className='main-header'>About Me</h1>
      <div className='about-container'>
        <div className='about-image'>
          <img src={about.data.image} alt='casual pose' />
        </div>
        <div className='about-content'>
          <h2>{about.data.header}</h2>
          <p>
            {about.data.content}
          </p>
        </div>
      </div>
    </div>
  );
}

export default About;