import './Card.scss';
import './ProjectCard.scss';
import React from 'react';

function Card(props) {
  return (
    <div className={`${props.className ? 'card ' + props.className : 'card'}`} >
      {props.children}
    </div>
  );
}

export function ProjectCard(props) {
  const { name, description, liveLink, gitLink, image } = props.project;

  return (
    <Card className={`${props.className ? 'project-card ' + props.className : 'project-card'}`}>
      <div className='container'>
        <div className="project-detail">
          <h2 className="card-header">{name}</h2>
          <p>{description}</p>
          <div className='project-links'>
            <a className='project-link' href={liveLink} target="_blank" rel="noopener noreferrer">View Live</a>
            <a className='project-source' href={gitLink} target="_blank" rel="noopener noreferrer">Source</a>
          </div>
        </div>
        <a className='project-image' href={liveLink} target="_blank" rel="noopener noreferrer">
          <img src={image} alt='project screencap' />
        </a>
      </div>
    </Card>
  );
}

export default Card;