import './Header.scss';
import '../styles.scss';
import { Link } from 'react-router-dom';
import { withRouter } from "react-router-dom";

function Header(props) {
  
  const isActiveLink = (match) => {
    const location = props.location.pathname.split('/')[1];
    return (match === location ? 'nav-link-active' : '')
  }

  return (
    <section className="header">
        <div className="main-container">
          <nav className="header-nav">
            <Link to='/projects' className={`nav-link ${isActiveLink('projects')}`}><span className='p'>Projects</span></Link>
            <Link to='/about' className={`nav-link ${isActiveLink('about')}`}><span className='a'>About</span></Link>
            <a href='#contact' className={`nav-link`}><span className='c'>Contact</span></a>
          </nav>
        </div>
    </section>
  );
}

export default withRouter(Header);
