import axios from "axios";
import { createContext, useContext, useEffect, useState } from "react";

const Context = createContext({});

export const ContextWrap = ({ children }) => {
    const [projects, setProject] = useState({ data: [], status: 'loading' });
    const [about, setAbout] = useState({ data: {}, status: 'loading' });
    const [minLoaded, setMinLoaded] = useState(false);
    const [appStatus, setAppStatus] = useState(false);

    useEffect(() => {
        const fetchData = async () => {
            const projectURL = process.env.REACT_APP_API_PROJECT;
            const aboutURL = process.env.REACT_APP_API_ABOUT;
            try {
                const _projects = await axios.get(projectURL);
                const _about = await axios.get(aboutURL);
                setProject({ data: _projects.data, status: 'ready' })
                setAbout({ data: _about.data, status: 'ready' })
            } catch (err) {
                setProject({ data: [], status: 'error' })
                setAbout({ data: {}, status: 'error' })
            }
        }

        fetchData();
    }, [])

    useEffect(() => {
        if (minLoaded && projects.status === 'ready') {
            setAppStatus('ready');
        } else if (minLoaded && projects.status === 'error') {
            setAppStatus('error');
        } else {
            setAppStatus('loading');
        }
    }, [minLoaded, projects])

    return (
        <Context.Provider
            value={{
                projects,
                about,
                setMinLoaded,
                appStatus
            }}
        >
            {children}
        </Context.Provider>
    )
}

export const useStateContext = () => useContext(Context);