import './App.scss';
import '../styles.scss';
import Header from '../Header/Header';
import Hero from '../Hero/Hero';
import Footer from '../Footer/Footer';
import AnimRoutes from '../AnimRoutes/AnimRoutes';
import { useStateContext } from '../../context/ContextWrap';
import Loading from '../Loading/Loading';
import { CSSTransition, SwitchTransition } from 'react-transition-group';
import { useRef } from 'react';

function App() {

  const { appStatus } = useStateContext();
  const transitionRef = useRef(null);

  const layout = (
    <>
      <Header />
      <Hero />
      <main>
        <div className="main-container">
          <AnimRoutes />
        </div>
      </main>
      <Footer />
    </>
  )

  return (
    <SwitchTransition>
      <CSSTransition
        key={appStatus === 'ready' ? 'ready' : 'loading'}
        nodeRef={transitionRef}
        addEndListener={(done) => transitionRef.current?.addEventListener("transitionend", done, false)}
        classNames='app'>
        <div className='app' ref={transitionRef}>
          {
            (appStatus === 'ready') ? layout : <Loading error={appStatus === 'error'} />
          }
        </div>
      </CSSTransition>
    </SwitchTransition >
  );
}

export default App;
